.contacts {
  z-index: 1000;
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  left: 0 !important;
  padding: 0;
  margin: 0;
  top: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  background-color: rosybrown;
}

.animation {
  animation: animation 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

@keyframes animation {
  0% {
    transform: rotateY(20deg) rotateX(-35deg) translate(300px, 300px) skew(35deg, -10deg);
    opacity: 0;
  }
  100% {
    transform: rotateY(0) rotateX(0deg) translate(0, 0) skew(0deg, 0deg);
    opacity: 1;
  }
}
